.pizzaBlock{
    margin: 0 50px 0 60px;
    padding-bottom: 100px;
    p{
        font-size: 32px;
        letter-spacing: 1%;
        line-height: 1.2;
        font-weight: 700;
        color: #000000;
        font-family: Nova;
        margin-bottom: 35px;
    }
    &__flex{
        display: flex;
        justify-content: flex-start;
        flex-wrap: wrap;
        gap: 50px;
        padding-left: 45px;
        @media(max-width: 1024px){
            justify-content: space-between;
            padding-left: 0;
        }
    }
}