.header {
  display: flex;
  justify-content: space-between;
  padding: 40px 40px 40px 77px;
  border-bottom: 1px solid #f6f6f6;
  line-height: 30px;
  @media (max-width: 768px) {
    padding: 20px 10px 20px 15px;
  }
  &--logo {
    position: relative;
    top: 3px;
    @media (max-width: 768px) {
    }
    .text {
      display: inline-block;
    }
    img {
      margin-right: 17px;
      position: relative;
      top: 5px;
      @media (max-width: 768px) {
        margin-right: 7px;
        height: 30px;
      }
    }
    h1 {
      font-family: Nova;
      font-size: 24px;
      font-weight: 700;
      line-height: 1.3;
      letter-spacing: 1%;
      text-transform: uppercase;
      color: black;
      text-decoration: none;
      @media (max-width: 768px) {
        font-size: 18px;
      }
    }
    p {
      font-family: Nova;
      font-size: 14px;
      font-weight: 400;
      line-height: 1.3;
      color: #7b7b7b;
      @media (max-width: 768px) {
        font-size: 12px;
      }
    }
  }
  .line {
    height: 25px;
    width: 1px;
    background-color: #fff;
  }
  &--button {
    background-color: #fe5f1e;
    border-radius: 30px;
    padding: 15px 23px;
    display: flex;
    color: #fff;
    gap: 13px;
    &:active{
      background-color: #d8531a;
    }
    @media (max-width: 768px) {
      padding: 8px 13px;
    }
    .price {
      display: inline-block;
      font-size: 16px;
      font-weight: 400;
      line-height: 1.2;
      position: relative;
      top: 3px;
      @media (max-width: 768px) {
        top: 7px;
        font-size: 12px;
      }
    }
    p {
      display: inline-block;
    }
  }
  .amount {
    position: relative;
    top: 3px;
    @media (max-width: 768px) {
      top: 4px;
    }
    img {
      margin-right: 8px;
      height: 14px;
      @media (max-width: 768px) {
        height: 12px;
        position: relative;
        top: 2px;
      }
    }
    p {
      font-size: 16px;
      font-weight: 400;
      line-height: 1.2;
      @media (max-width: 768px) {
        font-size: 13px;
      }
    }
  }
}
